.price-wrapper {
	.slider {
		position: relative;
		width: 100%;
		height: 6px;
		background: #d1d1d1;
		border-radius: 3px;
		margin: 30px 0;
		touch-action: none;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 3px;
			pointer-events: none;
		}
	}

	.slider-range {
		position: absolute;
		height: 100%;
		background: #000;
		border-radius: 3px;
		pointer-events: none;
		transition: all 0.2s ease;
	}

	.slider-thumb {
		width: 28px;
		height: 28px;
		background: #fff;
		border: 1px solid #1d1d1d;
		position: absolute;
		top: 50%;
		border-radius: 50%;
		transform: translate(-50%, -50%);
		cursor: pointer;
		z-index: 3;
		transition: all 0.2s ease;
		touch-action: none;

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			width: 12px;
			height: 12px;
			// background: #000;
			border-radius: 50%;
			transform: translate(-50%, -50%) scale(0);
			transition: transform 0.2s ease;
		}

		&:hover {
			transform: translate(-50%, -50%) scale(1.1);
			// box-shadow: 0 0 0 6px rgba(0, 0, 0, 0.05);

			&::after {
				transform: translate(-50%, -50%) scale(1);
			}
		}

		&:active,
		&:focus-visible {
			transform: translate(-50%, -50%) scale(1.2);
			// box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1);
			outline: none;

			&::after {
				transform: translate(-50%, -50%) scale(1);
			}
		}

		/* Focus styles for keyboard navigation */
		&:focus-visible {
			box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
		}
	}
}

